import React, { Component } from 'react'

class ConditionalGreetings extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             loggedIn: true
        }
    }
    
    render() {
        return this.state.loggedIn ?(
            <div>
                 yes
            </div>
        ): (
            <div>
                 no
            </div>
        )
    }
}

export default ConditionalGreetings
