import React, { Component } from 'react';

class Message extends Component {

    constructor(){
        super()
        this.state = {
            message: 'Welcome to this page!'
        }
    }

    changeMessage(){
        this.setState({
            message: 'Stupid dumbass'
        })
    }


    render(){
        return(
            <div>
                <h1>
                    {this.state.message}
                </h1>
                <button onClick = {() => this.changeMessage()}>
                    Press if you are dumb
                </button>
            </div>
        )
    }

}

export default Message