import React, { Component } from 'react'
import axios from 'axios'

class SpreadSheet extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             posts: []
        }
    }

    componentDidMount(){
        axios.get('https://spreadsheets.google.com/feeds/cells/1cOxqRbxTG_crYyEWRMvpAAVqYz-F22rKIHgDdSMfE7g/od6/public/basic?alt=json')
        .then(response => {
            console.log(response.data.feed.entry)
            this.setState({
                posts: response.data.feed.entry
            })
        })
        .catch(error => {
            console.log(error)
        })
    }
    
    render() {
        const{posts} = this.state
        return (
            <div>
                List of posts
                {
                    posts.length ?
                    posts.map(post => <div key = {post.id.$t}> {post.content.$t} </div>)
                    : <div> no data </div>
                }
            </div>
        )
    }
}

export default SpreadSheet
