import React, { Component } from 'react'
import Timer from './Timer'

class TimerApp extends Component {
    constructor(props) {
        super(props)
        this.componentRef = React.createRef()
        this.state ={
            runState: 0
        }
    }

    

    startTime = () =>{
        this.componentRef.current.startTime()
        this.setState({
            runState: 1
        })
    }

    stopTime = () =>{
        this.componentRef.current.stopTime()
        this.setState({
            runState: 2
        })
    }
    resetTime = () => {
        this.componentRef.current.resetTime()
        this.setState({
            runState: 0
        })
    }
    
    render() {
        const {runState} = this.state
        return (
            <>
            <Timer ref={this.componentRef}/>

            {runState == 0 &&
                <button onClick={this.startTime}>Start</button>
            }
            {runState == 1 && 
                <button onClick={this.stopTime}>Stop</button>
            }
            {runState == 2 && 
                <>
                <button onClick={this.startTime}>Resume</button>
                <button onClick={this.resetTime}>Reset</button>
                </>
            }

            </>
        )
    }
}

export default TimerApp
