import React, { Component } from 'react'

var incAmount = 60;

class Timer extends Component {
    constructor(props) {
        super(props)
        this.state = {
             ms: 0, 
             s:0, 
             m: 0,
             runState: 0
            //  0 is haven't started, 1 is running, 2 is paused
        }
    }

    startTime = () =>{
        this.myInterval = setInterval( ()=>{
            this.calculateNewStates()
         }, incAmount) 
         this.setState({
            runState: 1
        })
    }

    stopTime = () =>{
        clearInterval(this.myInterval)
        this.setState({
            runState: 2
        })
    }

    resetTime = () =>{
        this.setState({
            ms: 0, 
            s:0, 
            m: 0,
            runState: 0
        })
    }

    calculateNewStates(){
        const {ms,s,m} = this.state
        let mNew = m
        let sNew = s
        let msNew = ms+incAmount
        if (msNew>=1000){
            sNew++
            msNew-=1000
        }
        if (sNew>=60){
            mNew++
            sNew-=60
        }

        this.setState({
            ms: msNew,
            s: sNew,
            m: mNew
        })
        
    }
    
    render() {
        const {ms,s,m} = this.state
        const {size} = this.props
        let w = 1.5*size
        let w2 = 0.25*size
        return (

            <center><table style={{
                fontWeight: 'bold',
                fontSize: size,
                tableLayout: "fixed",
                textAlign: 'center',
                color: 'white'
            }}>
            <td style={{ width: w }}> {m<10? '0'+m : m} </td>
            <td style={{ width: w2 }}> : </td>
            <td style={{ width: w }}> {s<10? '0'+s : s} </td>
            <td style={{ width: w2 }}> : </td>
            <td style={{ width: w }}> {ms<100? '0'+ms/10 : ms/10} </td>
            </table></center>

        )
    }


}

export default Timer
