import React, { Component } from 'react'

class DumbTest extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             name: ''
        }
    }
    
    componentDidMount(){
        
        this.setState({
            // name: prompt("Name")
            name: 'Dumbo',
            clicked: false
        })
    }

    clickHandler = ()=>{
        this.setState({
            clicked: true
        })
    }

    render() {
        const {name, clicked} = this.state
        if (clicked){
            return(
                <h1>
                    HA DUMB
                </h1>
            )
        }else{
            return (
                <>
                <h1>
                    <div> Hi {name}! </div>
                    <div> Welcome to this page! </div>
                </h1>
                <button onClick={this.clickHandler}>Press this button if you are dumb</button>
                </>
            ) 
        }
    }
}

export default DumbTest
